@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    padding: 1rem;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: white;
  
}
.nav-container, .page-content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1700px;
    margin: 0 auto;
}

a{
    text-decoration: none;
}


.nav-center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.left-nav,
.right-nav {
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem 0rem 1.5rem;
}

.left-nav p,
.right-nav button {
  margin-right: 2rem;
  color: #000000;
  font-size: 1.2rem;
  text-transform: capitalize;
  cursor: pointer;
}

.active {
  font-weight: bold;
  margin-left: 1rem;
}

.right-nav .auth-btn {
  border: 1px solid black;
  padding: 0.5rem 1rem;
}

#btn {
  color: white;
  background-color: black;
  border: none;
}

.cart {
  position: relative;
}


.cart p{
  background-color: red;
  text-align: center;
  width: 30px;
  height: 10;
  color: white;
  font-weight: bold;
  padding: 0.1rem 0rem;
  border-radius: 100%;
  position: absolute;
  top: -40%;
  right: -40%;
}



.product{
    margin: 0 2rem 3rem 0;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
}

.img-container{
    width: 300px;
    padding-bottom: 2rem;

}

.image{
    width: 100%;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0 1rem 1rem 1rem;
}

.name-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.name-wrapper small{
    color: red;
    text-transform: capitalize;
    font-weight: bold;
}
.name{
    line-height: 3;
    font-size: 1.2rem;
    text-transform: uppercase;
}

.cart-btn{
    display: flex;
    align-items: center;
    background-color: #EEEEEE;
    border-radius: 25px;
    padding: 0.6rem 1rem;
    border: none;
    cursor: pointer;
}

.cart-btn img{
    width: 20px;
}

.cart-btn p{
    margin-left: 1rem;
    font-weight: bold;
}

.product-container{
    max-width: 1700px;
    margin: 0 auto;
}
.title{
    font-size: 2.5rem;
    line-height: 3;
    display: block;
}

.page-content{
    min-height: 80vh;
    width: 100%
}


.bike-slider img{
    width: 800px;
}

.page h1{
    font-size: 3.2rem;
    text-transform: capitalize;
    line-height: 2;
}

.page p{
    font-size: 1.5rem;
    color: gray;
    color: rgba(172,171,170,1);
    width: 600px;
    line-height: 1.8;
}

.search{
    margin-top: 2rem;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    display: flex;
    align-items: center;
    padding: 1rem;
}

.search input{
    width: 100%;
    padding: 0.5rem 0;
    font-size: 1.1rem;
    border: none;
    margin-right: 1rem;
    
}

.search input::-webkit-input-placeholder{
    font-size: 1.1rem;
    text-transform: capitalize;
}

.search input:-ms-input-placeholder{
    font-size: 1.1rem;
    text-transform: capitalize;
}

.search input::placeholder{
    font-size: 1.1rem;
    text-transform: capitalize;
}

.search button{
    padding: 0.5rem 1.2rem;
    text-transform: capitalize;
    font-size: 1rem;
    background-color: black;
    color: white;
    border: 1px solid black;
    cursor: pointer;
    transition: all 0.5s linear;
}

.search button:hover{
    background-color: white;
    color: black;
}

/* Product Item */


.products{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}




/* Review section */
.review-section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
}

.review-section div {
  margin-right: 5rem;
}

.review-section h1 {
  line-height: 2;
  text-transform: capitalize;
  font-weight: 400;
}

.review-section p {
  line-height: 2;
  font-size: 1.2rem;
}

.review-section p i {
  margin-right: 1rem;
}

.img-carousel {
  display: flex;
  align-items: center;
}

.img-carousel button {
  border: none;
  background-color: black;
  padding: 0.3rem 1rem;
  transition: all 0.5s linear;
  cursor: pointer;
  position: absolute;
  border-radius: 5px;
}

.right {
  left: 55%;
}

.left {
  left: 18%;
}

.img-carousel button i {
  color: white;
}

footer {
    padding: 1rem 2rem;
    width: 100%;
    height: auto;
    background: rgba(0, 0, 0, 0.03);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .flex-right {
    width: 70%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }
  
  .flex-right div {
    margin-right: 4rem;
  }
  
  .flex-right div button {
    color: black;
    line-height: 1.5;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 1rem;
    text-transform: capitalize;
  }
  
  .flex-left {
    width: 400px;
  }
  
  .nav-toggle {
    display: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  
  .nav-toggle i {
    font-size: 1.5rem;
  }
  
  .show {
    display: block;
  }
  
  @media (max-width: 900px) {
    .right-nav {
      display: none;
    }
  
    .nav-toggle {
      display: block;
      position: absolute;
      right: 4%;
      cursor: pointer;
      outline: none;
    }
  
    .left-nav {
      display: flex;
    }
  
    footer {
      display: block;
      height: auto;
    }
  
    .flex-left {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 60%;
      margin: 0 auto 3rem auto;
    }
  
    .flex-right {
      width: 100%;
      justify-content: center;
    }
  }
  
  @media (max-width: 700px) {
    .flex-left {
      width: 80%;
    }
  } 
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  outline: none;
  list-style: none;
}

